<template>
  <div class="test">
    <Button class="item" type="primary" @click="test_rank_update">添加关键词</Button>
  </div>
</template>

<script>
export default {
  name: 'test',
  data () {
    return {
    }
  },
  created: function () {
  },
  watch: {
  },
  methods: {
    test_rank_update: function () {
      var url = this.api_path('keyword/rank_update')
      this.axios.post(url, {
        keyword_id: 6658,
        site: 'bd',
        ranks: [
          {
            url: 'http://www.e0575.cn/syxw/2017/1123/21648597.html',
            rank: 2
          },
          {
            url: 'http://e.chengdu.cn/syxw/21712189.html',
            rank: 3
          }
        ]
      }).then(res => {
        console.log(res)
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



