<template>
  <div id="admin">
    <Layout>
      <Header :style="{background: '#00A4F5', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}"></Header>
      <Layout>
          <Sider collapsible :collapsed-width="78">
            <mu-list class="menu">
              <mu-list-item title="小程序" titleClass="menu-title" :open="false" toggleNested value="miniprograms">
                <mu-icon slot="left" value="phone_android"/>
                <mu-list-item slot="nested" title="应用" style="width: 200px;" value="apps" to="/admin/miniprograms">
                  <mu-icon slot="left" value="widgets"/>
                </mu-list-item>
                <mu-list-item slot="nested" title="案例" style="width: 200px;" value="cases" to="/admin/miniprograms/cases">
                  <mu-icon slot="left" value="grade"/>
                </mu-list-item>
              </mu-list-item>

              <mu-list-item title="客户管理" :to="{name: 'admin-custom'}">
                <mu-icon slot="left" value="supervisor_account"/>
              </mu-list-item>

              <mu-list-item title="云网客" :open="false" toggleNested>
                <mu-icon slot="left" value="wb_cloudy"/>
                <mu-list-item slot="nested" title="项目" style="width: 200px;" value="cases" :to="{name: 'admin-yunwangke'}">
                  <mu-icon slot="left" value="supervisor_account"/>
                </mu-list-item>
                <mu-list-item slot="nested" title="查词任务" style="width: 200px;" value="cases" :to="{name: 'admin-yunwangke-ranktool'}">
                  <mu-icon slot="left" value="supervisor_account"/>
                </mu-list-item>
                <mu-list-item slot="nested" title="合作网站" style="width: 200px;" value="cases" :to="{name: 'admin-yunwangke-partner'}">
                  <mu-icon slot="left" value="supervisor_account"/>
                </mu-list-item>
              </mu-list-item>

              <mu-list-item title="行业管理" to="/admin/industry">
                <mu-icon slot="left" value="work"/>
              </mu-list-item>
            </mu-list>
          </Sider>
          <Layout :style="{padding: '20px', backgroundColor: '#fff'}">
              <router-view name="main"></router-view>
          </Layout>
      </Layout>
  </Layout>

  </div>
</template>

<script>
export default {
  name: 'admin-main',
  components: {
  },
  data () {
    return {
    }
  },
  created: function () {
    console.log('this.$store.getters.loading')
    console.log(this.$store.getters.loading)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss">
.mu-tr, .mu-thead {
  border-bottom: 1px solid #dddee1;
}

.mu-tr:last-child {
  border: 0;
}

.menu {
  .mu-item-title {
    font-size: 14px;
    color: #fff;
  }

  .mu-list .mu-list {
    background-color: #3f495e;
  }

  .mu-item {
    min-height: 46px;
  }

  .mu-item.show-left {
    padding-left: 55px;

    i {
      font-size: 20px;
      color: #aeb9c2;
    }
  }

  .router-link-active {
    background-color: #00C1DE;

    .mu-item.show-left {
      i {
        color: #fff;
      }
    }
  }
}

// common
.table-header {
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #dddee1;

  .item {
    margin-right: 10px;
  }
}

.table-list {
  .handle {
    opacity: 0;
  }

  .hover .handle {
    opacity: 1;
  }
}

.table-footer {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #dddee1;
}
</style>

<style lang="scss" scoped>
#admin {
  display: flex;
  flex-wrap: wrap;
  height: 100%;

  .header {
    flex-grow: 0;
  }

  .warp {
    display: flex;
    flex-grow: 1;
    justify-content: flex-start;
    height: 100%;
  }
}

#loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



