<template>
  <div class="yunwangke">
    <div class="table-header">
      <Button class="item" type="primary" @click="add_dialog = true">添加项目</Button>
    </div>
    <mu-table class="table-list" multiSelectable enableSelectAll ref="table">
      <mu-thead>
        <mu-tr>
          <mu-th>客户</mu-th>
          <mu-th>云网客id</mu-th>
          <mu-th>行业</mu-th>
          <mu-th>案例</mu-th>
          <mu-th></mu-th>
        </mu-tr>
      </mu-thead>
      <mu-tbody>
        <mu-tr v-for="v of list.data">
          <mu-td><router-link :to="{name: 'admin-yunwangke-id', params: {id: v.id}}">{{v.name}}</router-link></mu-td>
          <mu-td>{{v.ywkid}}</mu-td>
          <mu-td>{{v.industry ? v.industry : '-'}}</mu-td>
          <mu-td><mu-icon value="done" :color="case_color(v.case)"/></mu-td>
          <mu-td>
            <ButtonGroup class="handle">
              <Button @click="add_task(v.id)">
                <Icon type="ios-cloud"></Icon>
                查词
              </Button>
              <Button :to="{name: 'admin-yunwangke-id', params: {id: v.id}}">
                <Icon type="chevron-left"></Icon>
                详情
              </Button>
              <Button :to="{name: 'admin-yunwangke-id', params: {id: v.id}}">
                <Icon type="trash-b"></Icon>
                删除
              </Button>
            </ButtonGroup>
          </mu-td>
        </mu-tr>
      </mu-tbody>
    </mu-table>

    <div class="table-footer">
      <!-- 分页 -->
      <mu-pagination :total="list.total" :current="list.current_page" @pageChange="pageto" :pageSize="pageSize"></mu-pagination>
    </div>
    <!-- 新增窗口 -->
    <mu-dialog :open="add_dialog" title="新增" @close="add_dialog = false">
      <mu-text-field label="所属客户" hintText="所属客户" v-model="add_data.customNmae" @focus="sel_custom_state = true" fullWidth labelFloat/><br/>
      <mu-text-field label="用户名" hintText="用户名" v-model="add_data.username" fullWidth labelFloat/><br/>
      <mu-text-field label="密码" hintText="密码" v-model="add_data.password" fullWidth labelFloat/><br/>
      <mu-text-field label="云网客id" hintText="云网客id" type="number" v-model="add_data.ywkid" fullWidth labelFloat/><br/>
      <mu-text-field label="所属行业" hintText="所属行业" v-model="add_data.industryName" @focus="sel_industry_state = true" fullWidth labelFloat/><br/>
      <mu-checkbox label="是否作为案例"  v-model="add_data.case"/> <br/>
      <mu-flat-button slot="actions" @click="add_dialog = false" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="submit" label="确定"/>
    </mu-dialog>

    <!-- 修改窗口 -->
    <mu-dialog :open="edit_dialog" title="编辑客户" @close="edit_dialog = false">
      <mu-text-field hintText="客户名称" v-model="edit_data.name" fullWidth /><br/>
      <mu-flat-button slot="actions" @click="edit_dialog = false" primary label="取消" />
      <mu-flat-button slot="actions" primary @click="editSubmit" label="确定" />
    </mu-dialog>

    <!-- 删除提示框 -->
    <mu-dialog :open="del_dialog" title="删除客户" @close="del_dialog = false">
      确定删除{{edit_data.name}}吗？<br/>
      <mu-flat-button slot="actions" @click="del_dialog = false" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="delSubmit" label="确定"/>
    </mu-dialog>

    <!-- 选择客户 -->
    <mu-dialog bodyClass="sel-custom-body" :open="sel_custom_state" title="选择客户" :scrollable="true" @close="sel_custom_state = false">
      <div class="warp">
        <mu-list class="demo-list">
          <mu-list-item v-for="v of customs.data" :key="v.id" :title="v.name" @click="sel_custom(v)"/>
        </mu-list>
        <!-- 分页 -->
        <mu-pagination :total="customs.total" :current="sel_custom_current" @pageChange="custom_page"></mu-pagination>
      </div>
      <mu-flat-button slot="actions" @click="sel_custom_state = false" primary label="取消"/>
    </mu-dialog>

    <!-- 选择行业 -->


    <!-- 选择行业 -->
    <el-selects :state="sel_industry_state" :data="industry.data" @close="sel_industry_state = false" @insert="industry_insert"></el-selects>
  </div>
</template>

<script>
import selects from '@/components/selects'

export default {
  name: 'yunwangke',
  components: {
    'el-selects': selects
  },
  data () {
    return {
      list: {},
      pageSize: 15,
      // 新增
      add_dialog: false,
      add_data: {},
      // 编辑
      edit_dialog: false,
      edit_data: {},
      del_dialog: false, // 删除
      industry: [],
      sel_industry_state: false, // 选择行业窗口状态
      customs: [], // 客户列表
      sel_custom_current: 1,
      sel_custom_state: false
    }
  },
  created: function () {
    this.get()
  },
  watch: {
    '$route' (to, from) {
      this.get()
    }
  },
  methods: {
    add_task: function (id) {
      var url = this.api_path('rankquery/add_task')
      this.axios.get(url, {
        params: {
          task_type: 'ywk',
          id: id,
          site: 'bd'
        }
      }).then(res => {
        this.success('任务已添加到系统')
      })
      .catch(e => {
        if (e.status === 400) {
          this.failed(e.data.message)
        } else {
          this.failed()
        }
      })
    },
    get: function () {
      this.axios.all(
        [
          this.get_list(),
          this.get_custom(),
          this.get_industry()
        ]
      )
      .then(this.axios.spread((list, customs, industry) => {
        this.list = list.data.data
        this.customs = customs.data
        this.industry = industry
      }))
    },
    // 分页
    pageto: function (page) {
      this.$router.push({name: 'admin-yunwangke-page', params: {page: page}})
    },
    case_color: function (is) {
      if (!is) {
        return '#bbb'
      } else {
        return '#2196f3'
      }
    },
    get_list: function () {
      var url = this.api_path('yunwangke/list')
      return this.axios.get(url, {
        params: {
          page: this.$route.params.page
        }
      })
    },
    industry_insert: function (info) {
      this.add_data.industryName = info.name
      this.add_data.industry = info.value
    },
    custom_page: function (page) {
      this.get_custom(page)
        .then(res => {
          this.customs = res.data
          this.sel_custom_current = page
        })
        .catch(e => {
          this.failed('客户加载失败，请重试。')
        })
    },
    sel_custom: function (info) {
      this.$set(this.add_data, 'customNmae', info.name)
      this.$set(this.add_data, 'customid', info.id)
      this.sel_custom_state = false
    },
    get_industry: function (page) {
      var url = this.api_path('industry')
      return this.axios.get(url, {
        params: {
          page: page,
          pagesize: 10
        }
      })
    },
    get_custom: function (page) {
      var url = this.api_path('custom/list')
      return this.axios.get(url, {
        params: {
          page: page,
          pagesize: 10
        }
      })
    },
    get_data: function () {
      var url = this.api_path('custom/list')
      return this.axios.get(url)
    },
    // 删除
    delSubmit: function () {

    },
    // 新增
    submit: function () {
      var url = this.api_path('yunwangke/add')
      this.axios.post(url, this.add_data)
        .then(res => {
          console.log(res)
        })
        .catch(e => {
          console.log(e)
        })
    },
    // 更新
    editSubmit: function () {

    }
  }
}
</script>

<style lang="scss">
</style>

<style lang="scss" scoped>
.yunwangke {
  .sel-custom-body {
    padding-top: 10px;
  }
}
  
</style>



