<template>
  <div class="partner">
    <div class="table-header">
      <Button class="item" type="primary" @click="add_state = true">添加网站</Button>
    </div>
    <mu-table class="table-list" multiSelectable enableSelectAll ref="table">
      <mu-thead>
        <mu-tr>
          <mu-th>任务名称</mu-th>
          <mu-th>查询类型</mu-th>
          <mu-th>网站</mu-th>
          <mu-th>状态</mu-th>
          <mu-th>操作</mu-th>
        </mu-tr>
      </mu-thead>
      <mu-tbody>
        <mu-tr v-for="v of list.data">
          <mu-td>{{v.name}}</mu-td>
          <mu-td>{{v.task_type}}</mu-td>
          <mu-td>{{v.site}}</mu-td>
          <mu-td>{{state(v.state).name}}</mu-td>
          <mu-td class="handle">
            <Icon type="compose"></Icon>
            <Icon type="android-delete"></Icon>
          </mu-td>
        </mu-tr>
      </mu-tbody>
    </mu-table>

    <div class="table-footer">
      <!-- 分页 -->
      <mu-pagination :total="list.total" :current="list.current_page" @pageChange="pageto" :pageSize="10"></mu-pagination>
    </div>
    
    <Modal
        v-model="add_state"
        title="新增合作网站"
        @on-ok="submit"
        @on-cancel="add_state = false">
        <Input v-model="add_data.name" placeholder="网站名称"></Input><br/><br/>
        <Input v-model="add_data.domain">
          <span slot="prepend" placeholder="网站域名">http://</span>
        </Input>
    </Modal>

  </div>
</template>

<script>
export default {
  name: 'ranktool',
  components: {
  },
  data () {
    return {
      list: [],
      add_state: false,
      add_data: {},
      delete_id: 0
    }
  },
  watch: {
    '$route' (to, from) {
      this.get()
    }
  },
  created: function () {
    this.get()
  },
  methods: {
    state: function (state) {
      var states = {
        '-1': {
          name: '失败'
        },
        '0': {
          name: '等待'
        },
        '1': {
          name: '进行中'
        },
        '11': {
          name: '完成'
        }
      }
      return states[state]
    },
    pageto: function (page) {
      this.$router.push({name: 'admin-yunwangke-partner-page', params: {page: page}})
    },
    submit: function () {
      var postdata = this.add_data
      var url = this.api_path('yunwangke/partner/add')
      this.axios.post(url, postdata).then(res => {
        this.success('添加成功')
      })
      .catch(e => {
        if (e.status === 400) {
          this.failed(e.data.message)
        } else {
          this.failed()
        }
      })
    },
    get: function () {
      var url = this.api_path('rankquery/list')
      this.axios.get(url).then(res => {
        this.list = res.data.data
        console.log(this.list)
      })
      .catch(e => {
        if (e.status === 400) {
          this.failed(e.data.message)
        } else {
          this.failed()
        }
      })
    },
    delete: function (id) {
      var url = this.api_path('yunwangke/partner/delete/' + id)
      this.axios.get(url).then(res => {
        this.success('删除成功')
      })
      .catch(e => {
        if (e.status === 400) {
          this.failed(e.data.message)
        } else {
          this.failed()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .partner {

  }
</style>



