<template>
  <div class="yunwangke">
    <div class="header">
      <Button class="item" type="primary" @click="export_excel">导出</Button>
      <Button class="item" type="primary" @click="add_task">自动查词</Button>
      <Button class="item" type="primary" @click="add_dialog = true">添加关键词</Button>
      <Select v-model="select_data.site" @on-change="select_change_site" class="item" placeholder="搜索引擎" style="width:100px">
        <Option v-for="(site, key) in select_data.sites" :value="key" :key="key">{{ site }}</Option>
      </Select>
      <Select v-if="select_data.site !== 'all'" @on-change="select_change_rank" v-model="select_data.rank" class="item" placeholder="筛选排名" style="width:80px">
        <Option v-for="(site, key) in select_data.ranks" :value="key" :key="key">{{ site }}</Option>
      </Select>
      <AutoComplete
        v-model="search_keyword"
        @on-search="search"
        icon="ios-search"
        placeholder="搜索关键词"
        @on-select="search_select"
        style="width:300px">
        <div class="keyword-search-item">
          <Option v-for="option in search_data.data" :value="option.keyword" :key="option.id">
            <span class="keyword-search-title">{{ option.keyword }}</span>
          </Option>
        </div>
        <router-link :to="{name: 'admin-yunwangke-search', params: {id: id, query: search_keyword}}" class="keyword-search-more">查看所有结果</router-link>
      </AutoComplete>
    </div>
    <mu-table multiSelectable enableSelectAll ref="table">
      <mu-thead>
        <mu-tr>
          <mu-th>关键词</mu-th>
          <mu-th>排名</mu-th>
          <mu-th>查询时间</mu-th>
        </mu-tr>
      </mu-thead>
      <mu-tbody>
        <mu-tr v-for="v of list.data">
          <mu-td>{{v.keyword}}</mu-td>
          <mu-td>
            <div class="">
              {{v.first_rank_bd}}
            </div>
            <!-- <div v-for="(v,k) of rankinfo(v.ranks)">
              <div :class="k">{{v.total}}</div>
            </div> -->
          </mu-td>
          <mu-td>{{v.updated_at}}</mu-td>
        </mu-tr>
      </mu-tbody>
    </mu-table>

    <div class="footer">
      <!-- 分页 -->
      <mu-pagination :total="list.total" :current="list.current_page" @pageChange="pageto" :pageSize="pageSize"></mu-pagination>
    </div>

    <!-- 新增窗口 -->
    <mu-dialog :open="add_dialog" title="新增" @close="add_dialog = false" :scrollable="true">
      <div class="add-warp" v-if="keyword_add_state">
        <br/>
        <mu-text-field hintText="请输入关键词" v-model="keyword_input" :errorText="keyword_add_err" :rows="6" :rowsMax="6" multiLine fullWidth/><br/>
        <mu-raised-button label="确认" @click="keyword_add" primary/><br/>
      </div>
      <div class="keyword-warp" v-if="!keyword_add_state">
        <br/>
        <mu-chip v-for="(word,i) in add_data.keywords" class="keyword-item" @delete="keyword_del(i)" showDelete>
          {{word}}
        </mu-chip>
      </div>
      <mu-flat-button slot="actions" @click="keyword_add_state = true" primary label="继续添加"/>
      <mu-flat-button slot="actions" @click="keyword_empty" primary label="清空关键词"/>
      <mu-flat-button slot="actions" @click="add_dialog = false" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="submit" label="确定"/>
    </mu-dialog>

    <!-- 修改窗口 -->
    <mu-dialog :open="edit_dialog" title="编辑客户" @close="edit_dialog = false">
      <mu-text-field hintText="客户名称" v-model="edit_data.name" fullWidth /><br/>
      <mu-flat-button slot="actions" @click="edit_dialog = false" primary label="取消" />
      <mu-flat-button slot="actions" primary @click="editSubmit" label="确定" />
    </mu-dialog>

    <!-- 删除提示框 -->
    <mu-dialog :open="del_dialog" title="删除客户" @close="del_dialog = false">
      确定删除{{edit_data.name}}吗？<br/>
      <mu-flat-button slot="actions" @click="del_dialog = false" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="delSubmit" label="确定"/>
    </mu-dialog>

  </div>
</template>

<script>
import selects from '@/components/selects'

export default {
  name: 'yunwangke',
  components: {
    'el-selects': selects
  },
  data () {
    return {
      id: this.$route.params.id,
      list: {},
      pageSize: 15,
      // 新增
      add_dialog: false,
      add_data: {
        type: 'ywk',
        parent: this.$route.params.id,
        keywords: []
      },
      // 编辑
      edit_dialog: false,
      edit_data: {},
      del_dialog: false, // 删除
      keyword_add_err: '', // 添加关键词错误提示
      industry: [],
      customs: [], // 客户列表
      sel_custom_current: 1,
      keyword_input: '',
      select_data: {
        site: 'all',
        rank: 'all',
        query: '',
        sites: {
          bd: '百度搜索',
          all: '全部'
        },
        ranks: {
          f10: '首页',
          f50: '前五页',
          b50: '无排名',
          all: '全部'
        }
      },
      keyword_add_state: true, // 显示添加关键词
      search_timeout: null,
      search_keyword: '',
      search_data: []
    }
  },
  created: function () {
    this.select_data.site = localStorage.ywkSite
    this.select_data.rank = localStorage.ywkRank

    this.init(this.$route.params)
  },
  watch: {
    '$route' (to, from) {
      this.init(to.params)
    }
  },
  methods: {
    // 导出excel
    export_excel: function () {
      var url = this.api_path('keyword/export/ywk/' + this.id + '?site=bd')
      window.location.href = url
    },
    // 添加自动查词任务
    add_task: function () {
      var url = this.api_path('rankquery/add_task')
      this.axios.get(url, {
        params: {
          task_type: 'ywk',
          id: this.id,
          site: 'bd'
        }
      }).then(res => {
        this.success('任务已添加到系统')
      })
      .catch(e => {
        if (e.status === 400) {
          this.failed(e.data.message)
        } else {
          this.failed()
        }
      })
    },
    init: function (params) {
      if (typeof params.query !== 'undefined' && params.query !== '') {
        this.$set(this.select_data, 'query', params.query)
      }
      this.get()
    },
    search_select: function (value) {
      this.$router.push({name: 'admin-yunwangke-search', params: {id: this.id, query: value}})
    },
    search_get: function () {
      var url = this.api_path('keyword/query')
      this.axios.get(url, {
        params: {
          query: this.search_keyword,
          type: 'ywk',
          parent_id: this.$route.params.id
        }
      }).then(search => {
        this.search_data = search.data.data
      })
      .catch(e => {
        this.failed('获取数据失败')
      })
    },
    search: function (query) {
      clearTimeout(this.search_timeout)
      this.search_timeout = setTimeout(() => {
        if (query === '') {
          this.$set(this.select_data, 'query', '')
          this.$router.push({'name': 'admin-yunwangke-id', params: {id: this.id}})
          return
        }
        this.search_keyword = query
        this.search_get()
      }, 500)
    },
    // 更新排名筛选信息
    select_change_rank: function (rank) {
      this.$set(this.select_data, 'rank', rank)
      localStorage.ywkRank = rank
      this.load_list()
    },
    // 更新站点筛选信息
    select_change_site: function (site) {
      this.$set(this.select_data, 'site', site)
      localStorage.ywkSite = site
      this.load_list()
    },
    // 去除重复关键词
    array_repeat: function (a) {
      var hash = []
      var arr = []
      for (var i = 0; i < a.length; i++) {
        var k = a[i]
        // hash[k] !== null
        if (!hash[k] && k !== '') {
          arr.push(k)
          hash[k] = true
        }
      }
      return arr
    },
    keyword_add: function () {
      var keywords = this.keyword_input.split(/\s+/)
      keywords = this.array_repeat(keywords) // 数组处理
      // 错误提示
      if (keywords.length < 1) {
        this.keyword_add_err = '关键词不能为空'
        return
      } else {
        if (this.keyword_add_err !== '') {
          this.keyword_add_err = ''
        }
      }
      var data = this.add_data.keywords
      keywords = this.array_repeat(data.concat(keywords)) // 数组处理
      this.$set(this.add_data, 'keywords', keywords)
      this.keyword_input = '' // 清空输入框
      this.keyword_add_state = false
    },
    keyword_empty: function () {
      this.keyword_add_state = true
      this.$set(this.add_data, 'keywords', [])
    },
    // 删除关键词
    keyword_del: function (index) {
      this.$delete(this.add_data.keywords, index)
    },
    // 排名信息转换
    rankinfo: function (data) {
      var info = {}
      for (var rank of data) {
        if (typeof info[rank.site] === 'undefined') {
          info[rank.site] = {}
          info[rank.site].total = 0
          info[rank.site].ranks = []
        }
        info[rank.site].total++
        info[rank.site].ranks.push(rank.rank)
      }
      return info
    },
    get: function () {
      this.axios.all(
        [
          this.get_list(),
          this.get_custom(),
          this.get_industry()
        ]
      )
      .then(this.axios.spread((list, customs, industry) => {
        this.list = list.data.data
        this.customs = customs.data
        this.industry = industry
      }))
    },
    // 分页
    pageto: function (page) {
      this.$router.push({name: 'admin-yunwangke-id-page', params: {page: page}})
    },
    case_color: function (is) {
      if (!is) {
        return '#bbb'
      } else {
        return '#2196f3'
      }
    },
    // 载入数据
    load_list: function () {
      this.get_list().then(list => {
        this.list = list.data.data
      })
      .catch(e => {
        this.failed('获取数据失败')
      })
    },
    // 获取数据
    get_list: function () {
      var url = this.api_path('yunwangke/single/' + this.$route.params.id)
      var params = {
        pagesize: 10
      }

      var rank = this.select_data.rank
      var site = this.select_data.site
      var query = this.select_data.query

      if (this.select_data.query !== '') {
        site = 'all'
        rank = 'all'
      }

      // 分页参数
      if (this.$route.params.page > 1) {
        params.page = this.$route.params.page
      }

      // 排名参数
      if (rank !== 'all') {
        params.rank = rank
      }

      // 搜索引擎参数
      if (site !== 'all') {
        params.site = site
      }

      // 搜索
      if (query !== '') {
        params.query = this.select_data.query
      }

      if (this.select_data.site === 'all' && this.select_data.rank !== 'all') {
        delete params.rank
      }

      console.log(params)

      return this.axios.get(url, {
        params
      })
    },
    get_industry: function (page) {
      var url = this.api_path('industry')
      return this.axios.get(url, {
        params: {
          page: page,
          pagesize: 10
        }
      })
    },
    get_custom: function (page) {
      var url = this.api_path('custom/list')
      return this.axios.get(url, {
        params: {
          page: page,
          pagesize: 10
        }
      })
    },
    get_data: function () {
      var url = this.api_path('custom/list')
      return this.axios.get(url)
    },
    // 删除
    delSubmit: function () {

    },
    // 新增
    submit: function () {
      var url = this.api_path('keyword/add')
      this.axios.post(url, this.add_data)
        .then(res => {
          this.success()
          this.load_list()
        })
        .catch(e => {
          if (e.status === 400) {
            this.failed(e.data.message)
          } else {
            this.failed()
          }
        })
    },
    // 更新
    editSubmit: function () {

    }
  }
}
</script>

<style lang="scss">

</style>

<style lang="scss" scoped>
.yunwangke {
  .sel-custom-body {
    padding-top: 10px;
  }

  .header {
    margin-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #dddee1;

    .item {
      margin-right: 10px;
    }
  }

  .footer {
    margin: 15px 0;
    padding: 15px 0;
    border-top: 1px solid #dddee1;
  }
}

.keyword-item {
  margin: 5px;
}

.keyword-search-item{
  padding: 4px 0;
  border-bottom: 1px solid #F6F6F6;
}
.keyword-search-group{
  font-size: 12px;
  padding: 4px 6px;
}
.keyword-search-group span{
  color: #666;
  font-weight: bold;
}
.keyword-search-group a{
  float: right;
}
.keyword-search-count{
  float: right;
  color: #999;
}
.keyword-search-more{
  display: block;
  margin: 0 auto;
  padding: 4px;
  text-align: center;
  font-size: 12px;
}
</style>



